import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/com_1/HelloWorld'
import content1 from '@/components/compont_one/content_1'
import content2 from '@/components/compont_one/content_2'
import content3 from '@/components/compont_one/content_3'
import content4 from '@/components/compont_one/content_4'
import content5 from '@/components/compont_one/content_5'
import content6 from '@/components/compont_one/content_6'
import content7 from '@/components/compont_one/content_7'
import content8 from '@/components/compont_one/content_8'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      components: {
        'default': content1,
        content2,
        content3,
        content4,
        content5,
        content6,
        content7,
        content8
      }
    }
  ]
})



